<template>
    <div class="cu-top">
        <div class="" style="display: flex">
            <el-card class="box-card user-card">
                <div slot="header" class="clearfix">
                    <div style="text-align: left">关于我</div>
                </div>
                <pan-thumb image="https://ae01.alicdn.com/kf/H2aef6ec6659640fe9cff77a03a91c2d7R.png" :height="'100px'"
                           :width="'100px'" :hoverable="false">
                    <div>牛皮</div>
                </pan-thumb>
                <div class="user-name">
                    {{ myData.first_name }}
                </div>
                <div style="color:#95a5a6;">
                    管理员
                </div>
                <div class="user-content">
                    <div class="user-content-intro">
                        <span class="el-icon-document">介绍</span>
                    </div>
                    <div class="user-content-txt">
                        我是一个{{myData.college.college_name}}管理员
                    </div>
                </div>
            </el-card>
            <el-card shadow="always" style="flex: 1;margin-left: 50px">
                <el-form label-position="top" label-width="80px" ref="numberValidateForm" :model="formLabelAlign" style="text-align: left">
                    <el-form-item label="用户名"
                                  prop="first_name"
                                  :rules="[{required: true, message: '用户名不能为空'}]">
                        <el-input v-model="formLabelAlign.first_name"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱">
                        <el-input v-model="formLabelAlign.email"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm">更新</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </div>
    </div>
</template>

<script>
    import PanThumb from './component/userCard'

    export default {
        name: "User",
        components: {
            PanThumb
        },
        data() {
            return {
                myData: {},
                formLabelAlign: {
                    first_name: null,
                    email: null,
                }
            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                this.myData = this.$store.state.token.userInfo;
                this.formLabelAlign.first_name = this.myData.first_name;
                this.formLabelAlign.email = this.myData.email;
            },
            sendSuccessMessage(){
                this.$notify({
                    title: '成功',
                    message: '更新成功',
                    type: 'success'
                });
            },
            submitForm(){
                this.$refs['numberValidateForm'].validate((valid) => {
                    if (valid) {
                        this.$api.UpdateUserData(this.myData.id,this.formLabelAlign).then(res=>{
                            if(res.status===200){
                               this.sendSuccessMessage();
                                 this.$store.commit('updateUserInfoData', res.data);
                                 localStorage.setItem('token', JSON.stringify(this.$store.state.token));
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .user-card {
        min-width: 200px;
        max-width: 400px;
        min-height: 600px;
        flex: 1;
    }

    .user-name {
        font-size: 20px;
        font-weight: bold;
        margin: 8px 0 5px;
    }

    .user-content {
        margin-top: 20px;
        text-align: left;
    }

    .user-content-intro {
        text-align: left;
        border-bottom: 1px solid #EBEEF5;
        color: #7f8c8d;
    }

    .user-content-txt {
        margin-top: 10px;
        color: #95a5a6;
    }

</style>